/*------------------------------------------------------------------
  [Buttons]
*/
.btn {
  box-shadow: $btn-box-shadow-base;
  @include button-size(
    $btn-padding-vertical,
    $btn-padding-horizontal,
    $btn-font-size-base,
    $btn-line-height-base,
    $btn-border-radius-base
  );

  &:active,
  &.active {
    box-shadow: none;
  }

  &:active:focus {
    outline: none;
  }
}

.btn-secondary,
.btn-light {
  color: $btn-secondary-color;
  background-color: $btn-secondary-bg;
  border-color: $btn-secondary-border;

  &:focus,
  &.focus {
    color: $btn-secondary-color;
    background-color: $btn-secondary-bg;
    border-color: $btn-secondary-border;
  }

  &:hover,
  &.hover {
    color: $btn-secondary-color;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
    background-color: $btn-secondary-bg;
    border-color: darken($btn-secondary-border, 13%);
  }

  &:active,
  &.active,
  &:not([disabled]):not(.disabled):active,
  &:not([disabled]):not(.disabled).active,
  .show > &.dropdown-toggle {
    box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.06);
    color: darken($btn-secondary-color, 10%);
    background-color: darken($btn-secondary-bg, 11%);
    border-color: darken($btn-secondary-border, 13%);

    &:hover,
    &:focus,
    &.focus {
      color: darken($btn-secondary-color, 10%);
      background-color: darken($btn-secondary-bg, 11%);
      border-color: darken($btn-secondary-border, 13%);
      box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.06);
    }
  }

  &:active,
  &.active,
  .show > &.dropdown-toggle {
    background-image: none;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    color: $btn-secondary-color;

    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      color: $btn-secondary-color;
      background-color: $btn-secondary-bg;
      border-color: $btn-secondary-border;
      box-shadow: none;

      .icon {
        color: $btn-secondary-color;
      }
    }
  }

  //icon color
  .icon {
    color: lighten($btn-secondary-color, 14%);
  }

  &:active {
    .icon {
      color: lighten($btn-secondary-color, 4%);
    }
  }

  .badge {
    color: $btn-secondary-bg;
    background-color: $btn-secondary-color;
  }
}

//Alternative color styles
.btn-primary {
  @include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border);
}

.btn-info {
  @include button-variant($btn-info-color, $btn-info-bg, $btn-info-border);
}

.btn-danger {
  @include button-variant($btn-danger-color, $btn-danger-bg, $btn-danger-border);
}

.btn-warning {
  @include button-variant($btn-warning-color, $btn-warning-bg, $btn-warning-border);
}

.btn-success {
  @include button-variant($btn-success-color, $btn-success-bg, $btn-success-border);
}

.btn-light {
  @include button-variant($btn-light-color, $btn-light-bg, $btn-light-border);
}

.btn-dark {
  @include button-variant($btn-dark-color, $btn-dark-bg, $btn-dark-border);
}

//Button sizes
.btn-xs {
  @include button-size(
    $btn-padding-vertical,
    $btn-xs-padding-horizontal,
    $btn-xs-font-size,
    $btn-xs-line-height,
    $btn-xs-border-radius
  );
}

.btn-sm {
  @include button-size(
    $btn-padding-vertical,
    $btn-sm-padding-horizontal,
    $btn-sm-font-size,
    $btn-sm-line-height,
    $btn-sm-border-radius
  );
}

.btn-lg {
  @include button-size(
    $btn-padding-vertical,
    $btn-lg-padding-horizontal,
    $btn-lg-font-size,
    $btn-lg-line-height,
    $btn-lg-border-radius
  );
  font-weight: $font-weight-medium;
}

.btn-xl {
  @include button-size(
    $btn-padding-vertical,
    $btn-xl-padding-horizontal,
    $btn-xl-font-size,
    $btn-xl-line-height,
    $btn-xl-border-radius
  );
  font-weight: $font-weight-medium;
}

//Button with rounded borders
.btn-rounded {
  border-radius: ceil(($btn-line-height-base + 2) / 2);

  &.btn-xs {
    border-radius: ceil(($btn-xs-line-height + 2) / 2);
  }

  &.btn-sm {
    border-radius: ceil(($btn-sm-line-height + 2) / 2);
  }

  &.btn-lg {
    border-radius: ceil(($btn-lg-line-height + 2) / 2);
  }
}

//Buton spacer helpers
.btn-hspace {
  margin-right: 5px;
}

.btn-vspace {
  margin-bottom: 5px;
}

.btn-space {
  margin-right: 5px;
  margin-bottom: 5px;
}

