//Icons inside buttons
.btn {
  .icon {
    font-size: 1.231rem;
    line-height: 15px;
    vertical-align: middle;

    &.icon-left {
      margin-right: $btn-icon-margin;
    }

    &.icon-right {
      margin-left: $btn-icon-margin;
    }

    &.fa {
      font-size: 1.154rem;
    }
  }

  &.btn-lg .icon {
    font-size: 1.615rem;
    line-height: 20px;
  }

  &.btn-sm .icon {
    font-size: 1.154rem;
    line-height: 15px;
  }

  &.btn-xs .icon {
    font-size: 1.077rem;
    line-height: 14px;
  }
}

//Butons dropdown
.btn.dropdown-toggle {
  .btn-group &.btn-primary,
  .btn-group &.btn-success,
  .btn-group &.btn-warning,
  .btn-group &.btn-danger {
    border-right-color: transparent;

    &:hover {
      border-right-color: transparent;
    }
  }

  .mdi-chevron-down,
  .mdi-chevron-up {
    font-size: 1.385rem;
    vertical-align: middle;
    line-height: 17px;
  }

  .icon-dropdown {
    font-size: 1.385rem;
    line-height: 17px;
    margin-left: 6px;

    &-left {
      margin-left: 0;
      margin-right: 6px;
    }
  }
}

//Big buttons
.btn-big {
  padding: 10px 13px;
  text-align: center;
  line-height: 13px;
  min-width: 62px;

  > .icon {
    display: block;
    font-size: 2rem;
    line-height: 20px;
    margin-bottom: 7px;
  }
}

//Button outline
.btn-outline {
  &.btn-primary {
    @include btn-transparent-outline($primary-color-text);
  }

  &.btn-secondary {
    @include btn-transparent-outline(darken(theme-color("light"), 15%));
  }

  &.btn-success {
    @include btn-transparent-outline($success-color-text);
  }

  &.btn-warning {
    @include btn-transparent-outline($warning-color-text);
  }

  &.btn-danger {
    @include btn-transparent-outline($danger-color-text);
  }
}
